
import Vue from 'vue';
import { CACHE_NAME } from '~/cache';

const Bus = new Vue({

    data() {
        return {
            cacheData: null,
            companyCache: []
        };
    },

    created() {
        // 绑定监听
        this.$on(CACHE_NAME.payParams, (val) => {
            this.cacheData = val;
            console.log('cacheData:');
            console.log(this.cacheData);
        });
        // 绑定监听
        this.$on(CACHE_NAME.addCompanyInOpenTask, (val) => {
            this.companyCache = val;
            console.log('companyCache:');
            console.log(this.companyCache);
        });
    }
});

export default Bus;



