import Vue from 'vue';
import Component from 'vue-class-component';

import popup from 'muse-ui/popup';
import flatButton from 'muse-ui/flatButton';

import 'vue-smooth-picker/dist/css/style.css';
import SmoothPicker from 'vue-smooth-picker';
import { Prop, Watch } from 'vue-property-decorator';
Vue.use(SmoothPicker);

import Utils from '~/utils';
import { provinces, citys } from '~/vendors/city';

export interface IPickerElement extends HTMLElement {
    getCurrentIndexList: Function;
    setGroupData: Function;
}

export interface IPickerEvent {
    theme?: string;
    groupIndex?: number;
    itemIndex?: number;
    picker?: IPickerElement;
    values?: any[];
    indexvalues?: any[];
}

export interface IPickerItem {
    currentIndex?: number;
    flex?: number; // 一个item的横向占位权重
    list?: any[]; // 数据列表
    onClick?: Function;
    textAlign?: string; // item内文字对齐 'center'  left、right
    className?: string; //
    divider?: boolean; // 是否为分隔排 如果是它的值为 true，那么它就是分割排，并且它的 onClick list currentIndex 将不管用
    text?: string; // 只能在 divider 为 true 是使用
}

@Component({
    components: {
        'mu-popup': popup,
        'mu-flat-button': flatButton
    }
})
export default class PickerComp extends Vue {

    @Prop()
    isShowPicker: boolean;

    @Prop()
    pickerData: IPickerItem[];

    @Prop()
    theme: string;

    @Prop()
    type: string;

    @Prop()
    initData: any[];

    $refs: {
        smoothPicker: any
    };

    // get picker() {
    //     return this.$refs.smoothPicker;
    // }

    cancel() {
        this.$emit('cancel');
    }

    confirm() {
        const values = this.getItemValue();
        if (this.type === 'date') {
            values[1] < 10 ? values[1] = '0' + values[1] : null;
            values[2] < 10 ? values[2] = '0' + values[2] : null;
        }
        const indexvalues = this.$refs.smoothPicker.getCurrentIndexList();
        const event: IPickerEvent = {
            theme: this.theme,
            values,
            picker: this.$refs.smoothPicker,
            indexvalues
        };
        this.$emit('confirm', event);
    }

    changeData(groupIndex: number, itemIndex: number) {
        const values = this.getItemValue();
        const event: IPickerEvent = {
            theme: this.theme,
            groupIndex,
            itemIndex,
            picker: this.$refs.smoothPicker,
            values
        };

        const valueIndexs = this.$refs.smoothPicker.getCurrentIndexList();
        if (this.type === 'date') {
            if ( groupIndex === 0 || groupIndex === 1) {
                const year = values[0];
                const month = values[1];
                const days = Utils.getContinuousArray(1, Utils.getMaxDate(year, month));
                const item: IPickerItem = {
                    currentIndex: 0,
                    list: days
                };
                this.$refs.smoothPicker.setGroupData(2, item);
            } 
        }

        if (this.type === 'city' && groupIndex === 0) {
            const item: IPickerItem = {
                currentIndex: 0,
                list: citys[values[0]]
            };
            this.$refs.smoothPicker.setGroupData(1, item);
        }
        this.$emit('change', event);
    }

    closeModel() {
        console.log('closeModel');
        this.$emit('cancel');
        // history.back();
    }

    getItemValue() {
        const ciList: number[] = this.$refs.smoothPicker.getCurrentIndexList();
        const values = [];
        const data = this.pickerData;
        for (let i = 0; i < ciList.length; i++) {
            values.push(data[i].list[ciList[i]]);
        }
        return values;
    }
}



