import Vue from 'vue';
import Component from 'vue-class-component';
import { Action } from 'vuex-class';
import Exif from 'exif-js';
import 'vue-croppa/dist/vue-croppa.css';
import Croppa from 'vue-croppa';
import { Prop } from 'vue-property-decorator';

Vue.use(Croppa);

export interface ICroppaOption {
    // 是否全屏
    fullScreen?: boolean;
    // 是否打开相机
    fromCamera?: boolean;
    // 压缩质量 0 ~ 1
    compressionRate: number;
    // 输出图片size倍数
    quality: number;
}

@Component({})
export default class CroppaComp extends Vue {

    @Action('showToast')
    showToast;

    @Action('showLoading')
    showLoading;

    @Action('hideLoading')
    hideLoading;

    isShow = false;

    width: number = document.body.clientWidth;

    height: number = document.body.clientWidth;

    windowWidth: number = null;

    windowHeight: number = null;

    croppaInst: any = {};

    @Prop()
    option: ICroppaOption;

    get inputAttrs() {
        if (this.option.fromCamera) {
            return {
                capture: 'camera'
            };
        } else {
            return {};
        }
    }

    get initSize() {
        if (this.option.fullScreen) {
            return 'contain';
        } else {
            return 'cover';
        }
    }

    get initPosition() {
        if (this.option.fullScreen) {
            return 'top';
        } else {
            return 'center';
        }
    }

    created() {
        // 设置初始化宽高
        const content = document.getElementsByClassName('view-container')[0];
        this.windowWidth = content.clientWidth;
        this.windowHeight = content.clientHeight;
        if (this.option.fullScreen) {
            this.width = this.windowWidth;
            this.height = this.windowHeight;
        }
    }

    chooseFile() {
        this.croppaInst.chooseFile();
    }

    cancel() {
        this.isShow = false;
        this.reSetCroppaSize();
        this.hideLoading();
        this.$emit('cancel');
    }

    hide() {
        this.isShow = false;
        this.hideLoading();
        this.reSetCroppaSize();
    }

    confirm() {
        // 在这里产生压缩数据，提交confirm事件
        this.showLoading();
        const base64Code = this.croppaInst.generateDataUrl('image/jpeg', this.option.compressionRate);
        const data = {
            base64Code,
            type: 'image/jpeg',
            filename: '123.jpg'
        };
        this.hideLoading();
        this.$emit('confirm', data);
    }

    choosedImg(file) {
        if (file) {
            // 在这个地方对图片进行预处理，设置裁剪框的宽高
            if (this.option.fullScreen) {
                const img = <HTMLImageElement>document.createElement('img');
                img.onload = () => {
                    // 根据图片设置宽高
                    this.setCropSizeByImg(img, file);
                };
                const url = this.getFileURL(file);
                img.src = url;
            } else {
                this.setCropDefaultSize();
            }
            this.croppaInst.refresh();
            this.isShow = true;
        }
    }

    reSetCroppaSize() {
        if (this.option.fullScreen) {
            this.width = this.windowWidth;
            this.height = this.windowHeight;
        }
    }

    getFileURL(file) {
        let url = null ;
        if (window['createObjectURL'] !== undefined) { // basic
            url = window['createObjectURL'](file) ;
        } else if (window.URL !== undefined) { // mozilla(firefox)
            url = window.URL.createObjectURL(file) ;
        } else if (window['webkitURL'] !== undefined) { // webkit or chrome
            url = window['webkitURL'].createObjectURL(file) ;
        }
        return url ;
    }

    setCropDefaultSize() {
        const windowWidth = document.body.clientWidth;
        this.width = windowWidth;
        this.height = windowWidth;
    }

    setCropSizeByImg(img: HTMLImageElement, file = null) {
        return new Promise((resolve, reject) => {
            // 在这里设置根据图片设置宽高
            //  需要读取可能存在图片旋转
            Exif.getData(file, () => {
                const Orientation = Exif.getTag(file, 'Orientation');
                // alert(Orientation);
                // 根据旋转信息来设置旋转
                const windowWidth = document.body.clientWidth;
                let imgWidth = img.width;
                let imgHeight = img.height;

                switch (Orientation) {
                case 6:
                    // 顺时针旋转90
                    imgWidth = img.height;
                    imgHeight = img.width;
                    break;
                case 8:
                    // 逆时针旋转90
                    imgWidth = img.height;
                    imgHeight = img.width;
                    break;
                case 3:
                    // 逆时针180
                    imgWidth = img.height;
                    imgHeight = img.width;
                    break;
                }
                const cropperWidth = windowWidth;
                const cropperHeight = windowWidth / imgWidth * imgHeight;
                this.width = cropperWidth;
                this.height = cropperHeight;
                resolve();
            });
        });
    }
}



